body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar{
    background-color: #458590;
}

.nav-link{
    color: #FFF;
}

.nav-link:hover{
    color: #7baca7;
}
.redirectToLevels {
  padding: 5px;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  color: #FFF;
  border: none;
  width: 100%;
}

#level-1 {
  background-color: #A7DDA7;
  color: #398689;
}

#level-1>h4 {
  color: #0b476d;
}

#level-2 {
  background-color: #b3daef;
  color: #447b98;
}

#level-2>h4 {
  color: #325A70;
}

#level-3 {
  background-color: #F2A03D;
}

#level-4 {
  background-color: #402921;
  color: #bfa393;
}
#level-navigation{
    margin: 0;
    padding: 0;
    min-width: 350px;
    text-align: center;
}

#level-navigation>li{
    display: inline;
    padding: 5px;
}

#level-navigation>li>a{
    display: inline;
    padding: 0;
}

.current, .current:hover{
    cursor: default;
    font-weight: bold;
}

#navbarDropdown{
    font-size: 13px;
    margin-top: 5px;
}

.circles{
    position: absolute;
    width: 3px;
    height: 3px;
    border: 2px solid #80a4be;
    border-radius: 50%;
    padding: 5px;
    bottom: 20%;
    right: 20%;
}

.circles::before{
    content: '';
    position: absolute;
    width: 8px;
    height: 8px;
    border: 2px solid #80a4be;
    border-radius: 50%;
    padding: 8px;
    top: -150%;
    left: 100%;
}

#codeDiv{
    height: 80%; 
    width: 28.5%; 
    float: left; 
    background-color: #ececec;
    font-family: monospace; 
    white-space: pre-wrap;
    border-right: 1px solid #d3d3d3;
}

#codeToHighlight{
    padding: 0;
}

#gameDiv{
    height: 80%;
    width: 21.5%;
    position: absolute;
    right: 0;
}

#gamePreview{
    width: 100%;
    height: 50%;
    background-color: black;
}

#mainDiv {
    height: 80%; 
    width: 50%; 
    float: left;
}

#helper{
    width: 78.5%;
    float: left;
    height: 20%;
    background-color: #21494b;
    color: #FFFFFF;
}

#helper-text{
    font-size: 14px;
}
html{
    background-color: #FFF;
}

.card>a>button {
    padding: 5px;
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
    color: #FFF;
    border: none;
    width: 100%;
}

.card-body {
    font-size: 12px;
}

.card{
    height: 100%;
}

.level-1 {
    background-color: #A7DDA7;
    color: #398689;
}

button {
    background-color: #398689;
}

@-webkit-keyframes highlight{
    0%{
        border-color: #78be97;
    }
    50%{
        border-color: #0b476d;
    }
    100%{
        border-color: #a7dda7;
    }
}

@keyframes highlight{
    0%{
        border-color: #78be97;
    }
    50%{
        border-color: #0b476d;
    }
    100%{
        border-color: #a7dda7;
    }
}

#final{
    -webkit-animation-name: highlight;
            animation-name: highlight;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    border-width: 2px;
}

.tooltip > .tooltip-inner > img{
    width: 100%;
}
.Landing{
    background-color: #398689;
    width: 100%;
    height: 100%;
    position: absolute;
}

.intro {
    position: absolute;
    top: 20%;
    width: 100%;
    height: 100%;
    text-align: center;
    color: #FFF;
}

.intro>pre{
    width: 10%;
    height: 100%;
    word-wrap: break-word;
}

#intro-start{
    background-color: #21494b;
    color: #FFF;
    border: 0;
    padding: 5px;
}

.Login{
    background-color: #398689;
    width: 100%;
    height: 100%;
    position: absolute;
}

#land{
    background-color: #A7DDA7;
    width: 100%;
    height: 20%;
}

.intro {
    position: absolute;
    top: 20%;
    left: 50%;
    width: 30%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height: auto;
    color: rgb(36, 36, 36);
    background-color: rgba(255, 255, 255, 0.329);
    padding: 10px;
    border-radius: 10px;
}

.intro>pre{
    width: 10%;
    height: 100%;
    word-wrap: break-word;
}

#intro-button{
    background-color: #21494b;
    color: #FFF;
    border: 0;
    padding: 5px;
}

.login-btn{
    border: 0;
    font-size: 14px;
    color: #FFF;
}

#login-google {
    background-color: #DD4B39;
}

#login-fb {
    background-color: #3b5998;
}

#login-gh {
    background-color: #6e5494;
}

#login-tt {
    background-color: #1da1f2;
}

#signup-list{
    list-style: none;
    width: 100%;
    padding: 0;
}

#signup-list li{
    display: inline-block;
    margin: 2px;
}
